import React, { Component } from "react";
import { Link, graphql, StaticQuery } from "gatsby";

import { rhythm, scale } from "../utils/typography";
import colors from "../utils/colors";

const styles = {
  headerRootStyle: {
    ...scale(1.5),
    marginBottom: rhythm(1.5),
    marginTop: 0
  },
  headerLinkStyle: {
    boxShadow: "none",
    textDecoration: "none",
    color: colors.pureApple
  },
  headerStyle: {
    fontFamily: "Montserrat, sans-serif",
    marginTop: 0
  },
  layoutContainer: {
    display: "flex",
    flexDirection: "column",
    marginLeft: "auto",
    marginRight: "auto",
    maxWidth: rhythm(24),
    padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
    minHeight: "100vh"
  },
  footer: {
    display: "flex",
    flexGrow: 0,
    justifyContent: "center"
  },
  socialLink: {
    color: colors.pureApple,
    padding: "0 10px"
  }
};

class Layout extends Component {
  render() {
    const { location, title, children } = this.props;
    const { headerRootStyle, headerLinkStyle, headerStyle, layoutContainer } = styles;
    const rootPath = `${__PATH_PREFIX__}/`;
    let header;

    if (location.pathname === rootPath) {
      header = (
        <h1 style={headerRootStyle}>
          <Link style={headerLinkStyle} to={"/"}>
            {title}
          </Link>
        </h1>
      );
    } else {
      header = (
        <h3 style={headerStyle}>
          <Link style={headerLinkStyle} to={"/"}>
            {title}
          </Link>
        </h3>
      );
    }
    return (
      <StaticQuery
        query={linksQuery}
        render={data => {
          const { twitter, github, email } = data.site.siteMetadata.social;
          return (
            <div style={layoutContainer}>
              <header>{header}</header>
              <main style={{ flexGrow: 1 }}>{children}</main>
              <footer style={styles.footer}>
                <a style={styles.socialLink} href={twitter} target="_blank">
                  twitter
                </a>{" "}
                &bull;{" "}
                <a style={styles.socialLink} href={github} target="_blank">
                  github
                </a>{" "}
                &bull;{" "}
                <a style={styles.socialLink} href={email} target="_blank">
                  email
                </a>
              </footer>
            </div>
          );
        }}
      />
    );
  }
}

const linksQuery = graphql`
  query LinksQuery {
    site {
      siteMetadata {
        social {
          twitter
          github
          email
        }
      }
    }
  }
`;

export default Layout;
